import parser from "cron-parser";
import cronstrue from "cronstrue/i18n";
import { settingsModule } from "@/store/modules";
import { distanceInWordsStrict } from "date-fns";

import enLocale from "date-fns/locale/en";
import deLocale from "date-fns/locale/de";
import { Language } from "@/shared";

// Format:
// *  *  *  *  *  *
//           
//           
//            day of week (0 - 7) (0 or 7 is Sun)
//          month (1 - 12)
//        day of month (1 - 31)
//      hour (0 - 23)
//    minute (0 - 59)
//  second (0 - 59, optional)

export const cleanCronExpression = (expr: string | undefined) =>
  (expr || "* * * * * *")
    .split(/\s/g)
    .map(s => s.trim())
    .filter(s => s && s.length)
    .join(" ");

export const nextCronExecutionDates = (
  expr: string | undefined,
  num: number
) => {
  const interval = parser.parseExpression(cleanCronExpression(expr));
  return new Array(num).fill(null).map(() => interval.next().toDate());
};

export const nextCronExecutionDate = (expr: string | undefined) => {
  const interval = parser.parseExpression(cleanCronExpression(expr));
  return interval.next().toDate();
};

export const datesInDistanceInWords = (rawDates: Date[]) =>
  rawDates.map(d =>
    distanceInWordsStrict(d, new Date(), {
      locale: settingsModule.language === Language.de ? deLocale : enLocale
    }).trim()
  );

export const humanReadableExpression = (
  expr: string | undefined
): string | false => {
  try {
    return cronstrue.toString(cleanCronExpression(expr), {
      use24HourTimeFormat: settingsModule.language === Language.de,
      locale: settingsModule.language
    });
  } catch (error) {
    return false;
  }
};
